import React from 'react'
import { graphql, StaticQuery } from 'gatsby'
import { Container, Grid, Typography, withStyles } from '@material-ui/core'

import SectionHeader from '../components/sections-header/section-header'
import Carousel from '../components/carousel/carousel'
import CarouselCard from '../components/carousel-card/carousel-card'

const styles = theme => ({
  root: theme.base.section.root,
  content: {
    ...theme.base.section.content,
    padding: theme.spacing(2, 0),
    margin: theme.spacing(2, 0),
  },
})

const SuccessStories = ({ classes }) => {
  return (
    <StaticQuery
      query={graphql`
        query CaseStudies {
          allCaseStudiesJson {
            nodes {
              title
              desc
              image
            }
          }
          allSectionsJson(filter: { id: { eq: "case-studies" } }) {
            nodes {
              title
              desc
            }
          }
        }
      `}
      render={data => {
        const { allSectionsJson, allCaseStudiesJson } = data
        const sectionDetails = allSectionsJson.nodes[0]
        const caseStudies = allCaseStudiesJson.nodes

        return (
          <Container className={classes.root}>
            <SectionHeader title={sectionDetails.title} />
            <Typography variant={'body1'} align={'center'} component={'span'}>
              {sectionDetails.desc}
            </Typography>

            <Grid
              container
              direction={'row'}
              justify={'space-between'}
              alignItems={'stretch'}
              className={classes.content}
              spacing={0}
            >
              <Grid item xs={12} style={{ width: '100%', height: 'auto' }}>
                <Carousel>
                  {caseStudies.map((caseStudy, index) => {
                    return <CarouselCard key={index} data={caseStudy} />
                  })}
                </Carousel>
              </Grid>
            </Grid>
          </Container>
        )
      }}
    />
  )
}

export default withStyles(styles)(SuccessStories)
